.viewParentDiv {
    padding-top: 4em;
    display: flex;
}

.viewParentDiv .imageShowDiv {
    padding: 16px;
    width: 70vw;
}

.viewParentDiv .imageShowDiv img {
    width: 65vw;
}

.viewParentDiv .rightSection {
    padding: 16px;
    margin-top: 3em;
    width: 30vw;
}

.viewParentDiv .rightSection .productDetails :first-child {
    font-weight: 700;
    margin-bottom: 8px;
    color: #002f34;
    font-size: 34px;
    line-height: 32px;
    margin-bottom: 20px;
}

.viewParentDiv .rightSection .productDetails {
    border: 1px solid black;
    padding: 6px;
    border-radius: 6px;
    margin-bottom: 2em;
    width: 100%;
}

.viewParentDiv .rightSection .contactDetails {
    border: 1px solid black;
    padding: 6px;
    border-radius: 6px;
}

.viewParentDiv .rightSection .contactDetails :first-child {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    color: #002f34;
    font-weight: 400;
    padding: 0;
}

.img_vi {
    width: 20px
}